
function InformacijePitanja() {

    window.scrollTo({
        top: 100,
        left: 100,
        behavior: 'smooth'
        });
      return (
        <div>
          <h3 style={{ marginTop:'1rem'}}>Najea pitanja</h3>
            <div>
            <strong>Da li mogu da vidim trenutnu sliku sadnice? </strong><br></br> <br></br>

            Ukoliko elite da Vam poaljemo kako sadnica izgleda u trenutku porudbine, potrudiemo se da Vam istu uslikamo i sliku poaljemo na Viber, 
            Messenger, MMS ili e-mailom. Izgled biljaka se menja u zavisnosti od godinjeg doba, a u vreme vegetacije i na nedeljnom nivou i iz tog razloga je nemogue
            u svakom momentu imati sliku sadnice, kako izgleda u momentu poruivanja. <br></br><br></br>

 

            <strong>Koliko brzo e narasti odreena biljka/drvo?</strong> <br></br> <br></br>

            Na ovo pitanje je jako nezahvalno davati odgovor jer brzina rasta zavisi i od uslova u kojima se biljka gaji kao to kvalitet zemljita, pozicija u bati,
            ubrenje, zalivanje itd. Za neke biljke je navedeno da su npr brzorastue ili koliko e veliinu dostii nakon odreenog broja godina, ali na to utiu i gore 
            navedeni faktori.  <br></br> <br></br>

            <strong>Kako da posadim biljku? </strong><br></br> <br></br>

            Prilikom sadnje je neophodno iskopati rupu odgovarajue veliine, te usitniti zemlju. U rupu sipati malo vode kako bi zemlja bila vlana.
            Nakon saenja je potrebno dobro pritisnuti zemlju okolo korena zatim dobro zaliti. Veoma je vano da oko korena ne ostane vazduni "dep". <br></br> <br></br>

 

            <strong>Da li je potrebno da ubrim biljke kako bi bolje rasle? </strong><br></br> <br></br>

            Sa ubrenjem ne moete pogreiti, jedino treba da vodite rauna o tome koje ubrivo koristite i da ga ne stavljate direktno na koren.
            Svako ubrivo se koristi drugaije, i najbolje je koristiti ga prema uputstvu proizvodjaa.<br></br> <br></br>  

            <strong>Da li mogu da sadim biljke u velike saksije/ardinjere? </strong><br></br> <br></br>

            Na rasadnik u ponudi uglavnom ima sadnice ije je idealno mesto sadnje u vrtu gde e sadnica i najbolje napredovati.
            Takoe skoro sve podnose i saenje u adekvatne saksije odnosno ardinjere. Ono to je veoma vano jeste: odabir odgovarajue veliine saksije,
            odabir dobre pozicije za biljku, pravilno zalivanje posebno u letnjem periodu, zbog breg isparavanje vode, zatite biljaka u saksijama od zime odnosno
            smrzavanja korena...
            </div>
        </div>
      );
    }
    
    export default InformacijePitanja;