

function InformacijePrivatnost() {

    window.scrollTo({
        top: 100,
        left: 100,
        behavior: 'smooth'
        });
      return (
        <div>
          <h3 style={{ marginTop:'1rem'}}>Politika privatnosti</h3>
            <div>
            Da bismo uspeno obradili Vau narudbinu potrebni su nam Vae ime i prezime, adresa, e-mail i telefon. Uz pomo tih podataka biemo u mogunosti da 
            Vam isporuimo eljenu robu, kao i da Vas obavestimo o trenutnom statusu narudbine.
            U nae ime obavezujemo se da emo uvati privatnost svih naih kupaca.<br></br><br></br>
            Prikupljamo samo neophodne, osnovne podatke o kupcima i podatke neophodne za 
            poslovanje i informisanje kupaca u skladu sa dobrim poslovnim obiajima i u cilju pruanja kvalitetne usluge. 
            Svi podaci o kupcima se strogo uvaju i dostupni su samo zaposlenima kojima su podaci nuni za obavljanje posla.<br></br><br></br>
            Obavezujemo se da prikupljene podatke ne koristimo 
            ni u koje druge svrhe, niti ih prosleujemo treim licima.<br></br><br></br>
            Rasadnik Ema zadrava pravo da odbije pruanje usluge ili otkae narudbenicu ukoliko ustanovi da je ve bilo nekorektne saradnje od strane kupca 
            (npr. ako smo sa istom osobom imali problem vie od dva puta nismo u obavezi da aljemo poiljku istoj).  
            </div>
        </div>
      );
    }
    
    export default InformacijePrivatnost;