

function InformacijeIsporuka() {

window.scrollTo({
    top: 100,
    left: 100,
    behavior: 'smooth'
    });
  return (
    <div>
      <h3 style={{marginTop:'1rem', color:'green'}}>Nain isporuke </h3>
        <div>
        Minimalni iznos porudbine ne moe da bude manji od 1000rsd. Porudbine koje nemaju naveden minimum nee se isporuivati. 
        Poiljke aljemo ISKLJUIVO PostExpress kurirskom slubom. Trokovi isporuke posebno se zaraunavaju i plaaju, u skladu sa cenovnikom Post Express 
        slube i iznose od 450,00 dinara, pa navie, zavisno od teine i vrednosti poiljke i nije mogue ih izraunati unapred. Maksimalna teina paketa za 
        slanje PostExpress slubom je 20 kg, u kutijama ije dimenzije ne prelaze 60x50x50cm.
        U suprotnom se poiljka alje kao CC paket, koja ima malo dui rok isporuke. Ukoliko ima vie paketa za svaki se plaaju trokovi isporuke.
        Nemamo mogunost slanja paketa u inostranstvo. Vreme isporuke je od 2-5 radnih dana.  <br></br><br></br>
        <strong>BESPLATNA DOSTAVA</strong> je mogua na teritoriji grada Novog Sada, za svaku narudbinu u vrednosti veoj od 2000rsd.


        <h3 style={{ color:'green'}}>Nain plaanja</h3> 

        Prilikom popunjavanja obrasca za poruivanje moete se odluiti za jedan od dva naina plaanja: plaanje pouzeem ili uplatnicom na tekui raun.
        Na tekui raun uplaujete samo iznos za poruene sadnice. Ptt trokove plaate prilikom preuzimanja poiljke sa sadnicama. 
        Ukoliko se odluite za plaanje pouzeem, iznos za sadnice i ptt trokove plaate prilikom preuzimanja u poti ili od kurira. 
        Kako bi poiljka bila sigurno i brzo dostavljena do kupca, potrebno je da kupac navede tane podatke prilikom formiranja porudbine. 
        </div>
    </div>
  );
}

export default InformacijeIsporuka;
